.SkillsBlock {
    min-height: 100vh;
    background-color: #d4ffc0;
}

.SkillsContainer {
    justify-content: flex-start;
    flex-direction: column;
}

.Skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
}